import kotlinx.css.Color
import kotlinx.css.backgroundColor
import kotlinx.css.color
import kotlinx.css.pct
import kotlinx.css.px
import kotlinx.css.width
import kotlinx.html.js.onClickFunction
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.ReactElement
import styled.css
import styled.styledButton
import styled.styledDiv

external interface TileRowProps: RProps {
    var tiles: List<TileItem>
    var tileTimerState: TileTimerState
    var onTileItemClick: (Int) -> Unit
}

class TileRow : RComponent<TileRowProps, RState>() {
    override fun RBuilder.render() {
        styledDiv {
            css {
                classes = mutableListOf("row m-0 pb-2")
            }
            for ((index, tileItem) in props.tiles.withIndex()) {
                styledDiv {
                    css {
                        classes = mutableListOf("col pr-2 pl-2")
                    }
                    styledButton {
                        css {
                            val btnStyle = if (props.tileTimerState.time > 0 && !tileItem.answered) "" else if (tileItem.answered) "btn-secondary" else "btn-danger"
                            classes = mutableListOf("btn $btnStyle mr-2")
                            if (props.tileTimerState.time > 0 && !tileItem.answered) {
                                val style = when (tileItem.type) {
                                    Topic.Athikaram -> "#f4be98"
                                    Topic.KuralSol -> "#85c9ee"
                                    Topic.Image -> "#c4bc29"
                                    Topic.KuralPorul -> "#f58849"
                                    else -> "#ff0000"
                                }
                                backgroundColor = Color(style)
                            }
                            width = 100.pct
                        }
                        +tileItem.type.tamil
                        attrs {
                            onClickFunction = {
                                props.onTileItemClick(index)
                            }
                        }
                    }
                }
            }
        }
    }
}

fun RBuilder.tileRow(handler: TileRowProps.() -> Unit): ReactElement {
    return child(TileRow::class) {
        this.attrs(handler)
    }
}
